.App {
  text-align: center;
  height: 100vh;
  background-color: pink;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.timer {
  display: flex;
  justify-content: center;
}

@keyframes rotating {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 500px){
  .img  {
    width: 300px;
    height: 300px;
  }
}


.rotate {
  animation: rotating 3s linear infinite;
  min-width: 10px;
}

@keyframes bounce {
  0% {
    top: 0
  }

  100% {
    top: 100%
  }
}

@keyframes bounce-y {
  0% {
    top: 0%;
    left: 0%
  }

  100% {
    top: 100%;
    left: 100%
  }
}

@keyframes bounce-right {
  0% {
    right: 0%;
    top: 0%
  }

  100% {
    right: 100%;
    top: 100%;
  }
}

.bouncy {
  position: absolute;
  animation: bounce 3s linear  infinite;
}
.bouncy-y {
  position: absolute;
  animation: bounce-y 3s linear infinite;
}
.bouncy-diag {
  position: absolute;
  animation: bounce-right 3s linear infinite;
}

@keyframes name {
  0% {
    left: 0%;
    bottom: 50%
  }

  100% {
    left: 100%;
    bottom: 50%;
  }
}

@media screen and (max-width: 500px) {
  .name {
    position: absolute;
    z-index: 100;
    font-size: 50px;
    font-weight: 200;
    background-image: linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(255,154,0,1) 10%, rgba(208,222,33,1) 20%, rgba(79,220,74,1) 30%, rgba(63,218,216,1) 40%, rgba(47,201,226,1) 50%, rgba(28,127,238,1) 60%, rgba(95,21,242,1) 70%, rgba(186,12,248,1) 80%, rgba(251,7,217,1) 90%, rgba(255,0,0,1) 100%);
    background-clip: text;
    color: transparent;
    animation: name 3s linear infinite;
  }
}


.name {
  position: absolute;
  z-index: 100;
  font-size: 100px;
  font-weight: 200;
  background-image: linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(255,154,0,1) 10%, rgba(208,222,33,1) 20%, rgba(79,220,74,1) 30%, rgba(63,218,216,1) 40%, rgba(47,201,226,1) 50%, rgba(28,127,238,1) 60%, rgba(95,21,242,1) 70%, rgba(186,12,248,1) 80%, rgba(251,7,217,1) 90%, rgba(255,0,0,1) 100%);
  background-clip: text;
  color: transparent;
  animation: name 3s linear infinite;
}



.countdown  {
  display: flex;
  align-items: center;
  margin-left: 3rem;
  font-size: 40px;
  min-width: 10px;
}
@media screen and (max-width: 500px){
  .countdown  {
  display: flex;
  align-items: center;
  margin-left: 3rem;
  font-size: 30px;
  min-width: 10px;
 }

 .rotate {
  animation: rotating 3s linear infinite;
  min-width: 10px;
  width: 300px;
  height: 300px;
}

}



